@import '../../styles/variables';

.singleView {
  .topContainer {
    height: 55vw;
    display: flex;
    align-items: flex-end;
    position: relative;
    max-height: 400px;

    > img {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .bottomGradient {
      position: absolute;
      bottom: 0;
      height: 60%;
      width: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(255, 255, 255, 0) 100%);
    }

    .bottomTopContainer {
      padding-bottom: 24px;
      padding-left: 15px;
      position: relative;

      > h2 {
        color: white;
      }
    }
  }

  .navContainer {
    display: flex;

    > button {
      background: transparent;
      padding: 16px 0;
      width: 50%;
      border-bottom: 1px solid #eee;

      &.active {
        border-bottom: 2px solid $golden;
      }
    }
  }

  .infoContainer {
    background-color: #f9f9f9;
    padding-top: 30px;

    .infoItem {
      background: white;
      padding-left: 15px;
      padding-top: 13px;
      padding-right: 15px;

      .infoItemContainer {
        display: flex;
        align-items: center;
        padding-bottom: 13px;
        border-bottom: 1px solid #eee;

        > a {
          text-decoration: none;

          &:hover {
            text-decoration: underline;
          }
        }
      }

      .iconContainer {
        width: 30px;
        display: flex;
        align-items: center;
      }

      .arrowIcon {
        margin-left: 15px;
        transform: rotate(90deg);
      }

      &:last-child {
        .infoItemContainer {
          border-bottom: 0;
        }
      }
    }
  }
}
