@import './variables';

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
span {
  font-weight: unset;
  margin: 0;
}

a {
  text-decoration: none;
}

h2 {
  font-family: $font-heading;
  font-size: 22px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.13px;
  color: #202020;
}

h3 {
  font-size: 20px;
  color: #202020;
  font-weight: 300;
  font-family: $font-heading;
}

h4 {
  font-size: 15px;
  font-family: $font-body;
  font-weight: normal;
  color: #202020;
}

h5 {
  font-size: 13px;
  font-family: $font-body;
  color: white;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
}

h6 {
  font-size: 14px;
  line-height: 18px;
  font-family: $font-body;
}

p,
span,
a {
  font-family: $font-body;
  font-size: 15px;
  line-height: normal;
  font-weight: 300;
  color: #202020;
}

div {
  box-sizing: border-box;
}

button {
  background: transparent;
  border: 0;
  padding: 0;
  margin: 0;
  outline: none;
}
